/**
 * take octo attributes,
 * generate a dialogue array based on which eyes/mouth/extra it has.
 */

export default class Dialogue {
  constructor(tokenData) {
    this.createdAt = tokenData.createdAt
    this.name = tokenData.metadata.name
    this.owner = tokenData.owner
    this.rarity = tokenData.rarity
    this.attributes = tokenData.metadata.attributes

    this.basic = [
      //owner
      this.owner.name !== null
        ? `I am ${this.name}.\nDid my owner call me?\n${this.owner.name}?`
        : `I am ${this.name}.\nDoes my owner have a name?`,
      //date
      `I was created on ${new Date(this.createdAt).toLocaleDateString()}.`,
      //rarity
      this.rarity !== null
        ? `I am ${Math.floor(this.rarity * 100)}% rare, so they say.`
        : `I don't know my rarity...That's ok.`,
    ]

    this.options = [
      // extra (1,2) - top octo
      `Who's on my head?`,
      // extra (3,4,5) - glass helmet
      `I don't want to take my helmet off yet.`,
      // eyes (2,23) - crying
      `I had a scary dream. Wait, was it a dream?`,
      // eyes (24) - one eye
      `I can see better because my eye is big.`,
      // eyes (7,14) - irritated
      `Who woke me up? I need more sleep.`,
      // eyes (20,21,22) - eye patch
      `I'm the coolest Octo in this room!`,
      // mouth (9,13) - smile
      `GM! Thanks for waking me up.`,
      // mouth (15) - blood
      `This is ketchup... it's not what you think it is...`,
      // mouth (16,17) - faciala hair
      `Should I keep my beard? Hmm...`,
      // mouth (18) - rainbow
      `I think I drank too much last night.`,
      // mouth (6) - dissatisfied
      `I'm hungry. I don't want to talk.`,
    ]

    this.special = []

    // extra
    switch (this.attributes[4].value) {
      case 1:
      case 2:
        this.special.push(this.options[0])
        break
      case 3:
      case 4:
      case 5:
        this.special.push(this.options[1])
        break
      default:
    }

    // eyes
    switch (this.attributes[1].value) {
      case 2:
      case 23:
        this.special.push(this.options[2])
        break
      case 24:
        this.special.push(this.options[3])
        break
      case 7:
      case 14:
        this.special.push(this.options[4])
        break
      case 20:
      case 21:
      case 22:
        this.special.push(this.options[5])
        break
      default:
    }

    // mouth
    switch (this.attributes[2].value) {
      case 9:
      case 13:
        this.special.push(this.options[6])
        break
      case 15:
        this.special.push(this.options[7])
        break
      case 16:
      case 17:
        this.special.push(this.options[8])
        break
      case 18:
        this.special.push(this.options[9])
        break
      case 6:
        this.special.push(this.options[10])
        break
      default:
    }
  }

  getDialogueBasic() {
    return this.basic
  }

  getDialogueSpecial() {
    // console.log(this.special);
    return this.special
  }
}
